import React, { useEffect } from 'react';
import roseImage from './rose.jpg';
import meImage from './me.jpg'

const DatePage = () => {
    useEffect(() => {
        // Create a <link> tag to load the Google Font
        const link = document.createElement('link');
        link.rel = 'stylesheet';
        link.href = 'https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&display=swap';
        
        // Append the link tag to the head of the document
        document.head.appendChild(link);
    
        // Clean up by removing the link when the component unmounts
        return () => {
          document.head.removeChild(link);
        };
      }, []);
    
      return (
        <div>
          <h1 style={{ fontFamily: 'Dancing Script, cursive', textAlign: 'center' }}>
            Hey cutie, would you like to go on a date with me tomorrow? 
          </h1>
          <div style={{ display: 'flex', justifyContent: 'center', gap: '20px' }}>
            <img 
              src={roseImage} 
              alt="Placeholder 1" 
              style={{ width: '40%', height: 'auto' }} 
            />
            <img 
              src={meImage} 
              alt="Placeholder 2" 
              style={{ width: '40%', height: 'auto' }} 
            />
          </div>
        </div>
      );
};

export default DatePage;